/* Login.css */
.login {
  font-family: Arial, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  background-color: #4caf50;
}

.login-container {
  width: 100%;
  max-width: 400px;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  text-align: center;
}

.login-container header h1 {
  font-size: 24px;
  margin-bottom: 20px;
  color: #333;
}

.login-container .login-form h2 {
  font-size: 20px;
  color: #555;
}

.login-container .login-form form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.login-container .login-form label {
  font-size: 18px;
  color: #333;
  text-align: left;
}

.login-container .login-form input[type="text"],
.login-container .login-form input[type="password"] {
  padding: 10px;
  font-size: 16px;
  border-radius: 4px;
  border: 1px solid #ddd;
}

.login-container .login-form input[type="text"]:focus,
.login-container .login-form input[type="password"]:focus {
  outline: none;
  border-color: #007bff;
}

.login-container .login-form button {
  padding: 10px;
  font-size: 16px;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.login-container .login-form button:hover {
  background-color: #0056b3;
}

.login-container .loginMessage-message {
  color: #ff4d4d;
  font-weight: bold;
  font-size: 18px;
  margin-top: 15px;
}
